<template>
	<view>
		<view class="container">
		  <!-- 功能列表 -->
		  <view class="center">
		    <view class="list">
		      <view class="list-info" @click='safetyCenter'>
		        <view class="left">安全中心</view>
		        <view class="right">
		          <image src="../../static/right.png"></image>
		        </view>
		      </view>
		      <view class="list-info last" @click='clearStorage'>
		        <view class="left">刷新缓存</view>
		        <view class="right">
		          <image src="../../static/right.png"></image>
		        </view>
		        <view class="right">{{currentSize}}KB</view>
		      </view>
		    </view>
		  </view>
			<!-- 退出当前账号 -->
			<view class="" style="margin-top: 18rpx;">
				<u-button text="退出当前账号"></u-button>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				currentSize:0,
			}
		},
		
		  onLoad() {
		    let storage = uni.getStorageInfoSync();
		    this.currentSize = storage.currentSize;
		  }, 
		
		methods: {
			  // 点击安全中心
			  safetyCenter(){
			    request.httpRquestCehckToken('user/safetyCenter', '', 'GET', function (res) {
			      if (res.code === 1) {
			        var h5url = encodeURIComponent(res.data.url);
			        uni.navigateTo({    // 跳转到h5页面进行支付
			          url: '/pages/user/safety/safety?h5url=' + h5url
			        })
			      }
			    });
			  },
			  //点击清除缓存
			  clearStorage(){
			    try {
			      uni.clearStorageSync();
			      uni.showToast({
			        title: '清理成功',
			        duration: 1000
			      })
			      this.currentSize = 0;
			    } catch (e) {
			      uni.showToast({
			        title: '清理失败',
			        icon: 'none',
			        duration: 1000
			      })
			    }
			  }
		}
	}
</script>

<style scoped>
.center .list{
  width:100%;
  background: #fff;
  margin-top:15rpx;
}
.center .list .list-info{
  width:100%;
  height:85rpx;
  border-bottom:1rpx solid #f6f6f6;
}
.center .list .last{
  border-bottom:0;
}
.center .list view{
  width:150rpx;
  height: 85rpx;
  text-align: center;
  line-height: 85rpx;
}
.center .list .left{
  float:left;
  font-size:25rpx;
}
.center .list .list-info .left-icon{
  background-repeat: no-repeat;
  background-position:center;
  width: 85rpx;
  height:85rpx;
  background-size:50% 50%;
}
.center .list .right{
  float:right;
  font-size:26rpx;
  color:gray;
  margin-right:8rpx;
  background-repeat: no-repeat;
  background-position:center;
  width: 85rpx;height:85rpx;      
  background-size:35% 35%;
}
.center .list .right image{width:40%;height:30%;}

::v-deep .u-button__text {
	color: #FE542C;
}
</style>
